"use client"

import { useState, useEffect } from "react"
import { useRouter } from "next/navigation"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import { 
  Plus, Play, Square, Trash2, Edit, Send, 
  Slack, LogOut, Activity, BarChart3, Users, Loader2
} from "lucide-react"

interface Promotion {
  id: number;
  title: string;
  points: number;
  limit: number;
  status: "READY" | "ACTIVE" | "FINISHED";
}

export default function AdminDashboard() {
  const router = useRouter()
  const [isAuthorized, setIsAuthorized] = useState(false)
  const [promotions, setPromotions] = useState<Promotion[]>([
    { id: 1, title: "선착순 1000명 포인트 증정", points: 5000, limit: 1000, status: "READY" },
    { id: 2, title: "오픈 기념 웰컴 포인트", points: 3000, limit: 500, status: "ACTIVE" },
  ])

  // --- 권한 체크 로직 추가 ---
  useEffect(() => {
    const role = localStorage.getItem("role")
    if (role !== "admin") {
      alert("관리자 권한이 없습니다.")
      router.replace("/login") // replace를 사용하여 뒤로가기 방지
    } else {
      setIsAuthorized(true)
    }
  }, [router])

  const toggleStatus = (id: number, status: "ACTIVE" | "FINISHED") => {
    setPromotions(prev => prev.map(p => p.id === id ? { ...p, status } : p))
  }

  const handleDelete = (id: number) => {
    if(confirm("정말 삭제하시겠습니까?")) setPromotions(prev => prev.filter(p => p.id !== id))
  }

  const handleLogout = () => {
    localStorage.clear()
    router.push("/login")
  }

  const sendSlackTest = () => alert("Slack으로 [시스템 연결 테스트] 알림을 보냈습니다.")
  const sendSlackReport = () => alert("Slack으로 [서버 리포트] 보고서를 발송했습니다.")

  // 권한 확인 전 로딩 화면
  if (!isAuthorized) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-slate-50">
        <Loader2 className="h-8 w-8 animate-spin text-primary" />
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-slate-50 flex">
      <aside className="w-64 bg-slate-900 text-white p-6 hidden md:block">
        <h2 className="text-xl font-bold mb-10 text-primary italic">Next Me Admin</h2>
        <nav className="space-y-2">
          <div className="p-3 bg-primary/20 rounded-lg text-primary-foreground flex items-center gap-2 font-medium cursor-default">
            <BarChart3 size={18} /> 프로모션 관리
          </div>
          <button 
            className="w-full p-3 hover:bg-white/10 rounded-lg flex items-center gap-2 cursor-pointer transition-colors"
            onClick={handleLogout}
          >
            <LogOut size={18} /> 로그아웃
          </button>
        </nav>
      </aside>

      <main className="flex-1 p-8 text-slate-900">
        <header className="flex justify-between items-center mb-8">
          <div>
            <h1 className="text-3xl font-bold">프로모션 대시보드</h1>
            <p className="text-slate-500 mt-1 font-medium">관리자 전용 이벤트 관리 센터</p>
          </div>
          <div className="flex gap-3">
            <Button variant="outline" className="border-green-600 text-green-600 hover:bg-green-50" onClick={sendSlackTest}>
              <Slack className="mr-2 h-4 w-4" /> 연결 테스트
            </Button>
            <Button variant="default" className="bg-[#4A154B] hover:bg-[#3b113b]" onClick={sendSlackReport}>
              <Send className="mr-2 h-4 w-4" /> 리포트 전송
            </Button>
          </div>
        </header>

        <div className="grid grid-cols-1 md:grid-cols-2 gap-6 mb-8">
          <Card className="p-6 flex items-center gap-4 border-l-4 border-l-green-500 shadow-sm">
            <div className="p-3 bg-green-100 rounded-full text-green-600"><Activity size={24}/></div>
            <div><p className="text-sm text-slate-500 font-bold">서버 상태</p><p className="text-xl font-black">Healthy</p></div>
          </Card>
          <Card className="p-6 flex items-center gap-4 border-l-4 border-l-primary shadow-sm">
            <div className="p-3 bg-primary/10 rounded-full text-primary"><Users size={24}/></div>
            <div><p className="text-sm text-slate-500 font-bold">활성 이벤트</p><p className="text-xl font-black">{promotions.filter(p=>p.status==="ACTIVE").length} 건</p></div>
          </Card>
        </div>

        <Card className="overflow-hidden bg-white shadow-md border-0">
          <div className="p-6 border-b flex justify-between items-center bg-white">
            <h3 className="font-bold text-lg">이벤트 목록</h3>
            <Button size="sm" className="font-bold"><Plus className="mr-2 h-4 w-4" /> 신규 이벤트 생성</Button>
          </div>
          <div className="overflow-x-auto">
            <table className="w-full text-left">
              <thead className="bg-slate-50 text-slate-500 text-xs font-bold uppercase tracking-wider">
                <tr>
                  <th className="px-6 py-4 font-black">제목</th>
                  <th className="px-6 py-4 font-black">포인트</th>
                  <th className="px-6 py-4 font-black">제한</th>
                  <th className="px-6 py-4 font-black">상태</th>
                  <th className="px-6 py-4 font-black text-center">관리</th>
                </tr>
              </thead>
              <tbody className="divide-y divide-slate-100">
                {promotions.map((p) => (
                  <tr key={p.id} className="hover:bg-slate-50/50 transition-colors">
                    <td className="px-6 py-4 font-semibold text-slate-800">{p.title}</td>
                    <td className="px-6 py-4 text-blue-600 font-black">{p.points.toLocaleString()}P</td>
                    <td className="px-6 py-4 font-mono font-bold">{p.limit}명</td>
                    <td className="px-6 py-4">
                      {p.status === "READY" && <Badge variant="secondary" className="font-bold">준비중</Badge>}
                      {p.status === "ACTIVE" && <Badge className="bg-green-500 text-white font-bold">진행중</Badge>}
                      {p.status === "FINISHED" && <Badge variant="destructive" className="font-bold">종료됨</Badge>}
                    </td>
                    <td className="px-6 py-4">
                      <div className="flex justify-center gap-2">
                        {p.status === "READY" && (
                          <Button size="icon" variant="ghost" className="text-green-600 hover:bg-green-50" onClick={() => toggleStatus(p.id, "ACTIVE")} title="이벤트 시작">
                            <Play size={18} fill="currentColor" />
                          </Button>
                        )}
                        {p.status === "ACTIVE" && (
                          <Button size="icon" variant="ghost" className="text-red-600 hover:bg-red-50" onClick={() => toggleStatus(p.id, "FINISHED")} title="이벤트 종료">
                            <Square size={18} fill="currentColor" />
                          </Button>
                        )}
                        <Button size="icon" variant="ghost" className="text-slate-400 hover:text-primary transition-colors"><Edit size={18}/></Button>
                        <Button size="icon" variant="ghost" className="text-slate-400 hover:text-destructive transition-colors" onClick={() => handleDelete(p.id)}>
                          <Trash2 size={18}/>
                        </Button>
                      </div>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </Card>
      </main>
    </div>
  )
}